@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@700&display=swap");

.categoriesSolo {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 80px;
  h1 {
    font-family: Montserrat Alternates;
    font-size: 36px;
    font-weight: 700;
    line-height: 44px;
    letter-spacing: 0em;
    color: #263238;
    margin-bottom: 50px;
  }
  .items {
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 50px;
    flex-wrap: wrap;
    overflow-x: auto;
    &::-webkit-scrollbar {
      display: none;
      padding-bottom: 30px;
    }
    .item {
      display: flex;
      flex-direction: column;
      width: 295px;
      &:hover {
        transform: scale(1.1);
        transition: 0.5s;
        box-shadow: 0px 0px 14px 7px rgba(219, 219, 219, 0.75);
        -webkit-box-shadow: 0px 0px 14px 7px rgba(219, 219, 219, 0.75);
        -moz-box-shadow: 0px 0px 14px 7px rgba(219, 219, 219, 0.75);
      }
      &__top {
        display: flex;
        flex-direction: column;
        img {
          width: 295px;
          height: 179px;
          border-radius: 6px 6px 0px 0px;
        }
        span {
          font-family: Montserrat Alternates;
          font-size: 15px;
          font-weight: 700;
          line-height: 18px;
          letter-spacing: -0.01em;
          text-align: left;
          color: #263238;
          width: 295px;
          height: 54px;
        }
      }
      &__bottom {
        display: flex;
        gap: 30px;
        margin: 38px auto 14px;
        .price {
          font-family: Montserrat Alternates;
          font-size: 22px;
          font-weight: 700;
          line-height: 27px;
          letter-spacing: -0.01em;
          text-align: center;
          color: #2196f3;
        }
        .notprice {
          font-family: Montserrat Alternates;
          font-size: 14px;
          font-weight: 700;
          line-height: 17px;
          letter-spacing: -0.01em;
          text-align: center;
          color: #b3b3b3;
          text-decoration: line-through;
        }
        .mentor {
          justify-content: flex-end;
          font-family: Montserrat Alternates;
          font-size: 16px;
          font-weight: 400;
          line-height: 20px;
          letter-spacing: -0.01em;
          text-align: center;
        }
      }
    }
  }
}
